@import "index";

html {
  padding: 0; }

body {
  font-size: 1.06em;
  margin: 0 0 0 16.5em;
  max-width: inherit;
  overscroll-behavior: none; }

body > header {
  display: block;
  position: fixed;
  overflow: auto;
  background-image: url("background.jpg");
  background-color: darken($dark, 2%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  border: none;
  box-shadow: none;
  margin: 0;
  padding: 2.25em;
  width: 14em;
  top: 0;
  bottom: 0;
  left: 0; }

body > footer {
  display: none; }

body > article {
  margin: 0 8em;
  padding: 4em 0;
  max-width: 50em; }

aside {
  display: block!important;
  position: fixed;
  overflow: auto;
  background: darken($dark, 2%);
  color: #fff;
  padding: 2.25em;
  width: 12em;
  top: 0;
  bottom: 0;
  left: 0; }

article header h1 {
  font-size: 2em; }

nav {
  margin: 0 -2.25em; }

nav a {
  display: block;
  margin: 0;
  padding: .5em 2.25em; }

nav a:hover, nav a.selected {
  background: lighten($dark, 2%);
  text-decoration: inherit; }

.icon {
  margin-right: 1.2em;
  color: inherit!important; }



.title {
  display: block;
  font-size: 1.5em;
  margin-bottom: 1em; }




